const map = {
  202: '用户名已被占用',
  203: '电子邮箱地址已经被占用',
  204: '没有提供电子邮箱地址',
  205: '找不到电子邮箱地址对应的用户',
  210: '用户名和密码不匹配',
  211: '找不到用户',
  217:  '无效的用户名，不允许空白用户名',
  218: '无效的密码，不允许空白密码',
  unknown:  '请求失败，请稍后再试'
};

export default function(code){
  return  map[code] || map.unknown
}



